  .custom-loader {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
    padding: 12px 0;
    width:auto;
    height:40px;
    -webkit-mask:linear-gradient(90deg,#77737a 70%,#0000 0) left/20% 100%;
    background:
     linear-gradient(#6c6a6e 0 0) left -25% top 0 /20% 100% no-repeat
     #9ee7f4;
    animation:ct7 1s infinite steps(6);
  }
  @keyframes ct7 {
      100% {background-position: right -25% top 0}
  }